body::-webkit-scrollbar {
  display: none;
}
body {
  -ms-overflow-style: none;
}
.Home {
  background: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.Home .fork-me-on-github {
  position: absolute;
  top: 0;
  right: 0;
}
.Home .body {
  display: flex;
  justify-content: center;
  width: 99vw;
}
.Home .body .container {
  margin-top: 10vh;
  margin-bottom: 10vh;
  display: flex;
  width: 640px;
  flex-direction: column;
  align-items: center;
}
.Home .body .container .title {
  font-size: 60px;
  font-weight: bold;
}
.Home .body .container .slogan {
  margin-top: 20px;
  width: 640px;
  font-size: 24px;
  display: flex;
  justify-content: center;
}
.Home .body .container .desc {
  margin-top: 20px;
}
.Home .body .page-content {
  padding: 80px 8vw 60px;
}
.Home .body .logo {
  margin-top: 10vh;
  margin: 24px 0;
}
.Home .body .download-container {
  display: flex;
  flex-wrap: nowrap;
}
.Home .body .download-container .download-button {
  text-decoration: none;
  min-width: 120px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
  background-image: -webkit-linear-gradient(top, #f8f8f8, #f1f1f1);
  background-color: #f8f8f8;
  border: 1px solid #c6c6c6;
  color: #222;
  border-radius: 4px;
  color: #5f6368;
  font-family: arial, sans-serif;
  font-size: 14px;
  line-height: 16px;
  margin: 11px 4px;
  padding: 0 16px;
  height: 36px;
  text-align: center;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.Home .body .download-container .download-button-icon {
  width: 16px;
  height: 16px;
  margin-right: 2px;
}
.Home .footer {
  height: 80px;
  display: flex;
  justify-content: center;
  bottom: 0;
  flex-wrap: wrap;
  width: 300px;
}
.Home .footer .comment {
  width: 300px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.Home .footer .comment .tip {
  color: #5c5edc;
  font-size: 16px;
  text-decoration: none;
}
.Home .footer .comment p {
  margin: 0;
}
.Home .footer .comment .row {
  display: flex;
  width: 300px;
  justify-content: space-between;
}
.Home .footer .thank-list {
  margin-top: 20px;
  margin-bottom: 20px;
  display: flex;
  width: 300px;
  flex-wrap: wrap;
}
.Home .footer .thank-list-item {
  margin-top: 10px;
  width: 300px;
  justify-content: space-between;
  display: flex;
}
